body {
  min-width: 250px;
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

.header {
  color: #fff;
  text-align: center;
  background-color: #f44336;
  padding: 30px 40px;
}

.header:after {
  content: "";
  clear: both;
  display: table;
}

.title {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 30px;
}

input {
  width: 75%;
  float: left;
  border: none;
  border-radius: 5px 0 0 5px;
  margin: 0;
  padding: 10px;
  font-size: 18px;
}

.addBtn {
  width: 25%;
  color: #555;
  float: left;
  text-align: center;
  cursor: pointer;
  background: #d9d9d9;
  border-radius: 0 5px 5px 0;
  padding: 10px;
  font-size: 18px;
  transition: all .3s;
}

.addBtn:hover {
  color: #000;
  background-color: #bbb;
}

.item {
  background: #eee;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  padding: 5px 40px;
  font-size: 18px;
  list-style-type: none;
  transition: all .2s;
  display: flex;
  position: relative;
}

.item:nth-child(2n+1) {
  background: #f9f9f9;
}

.item:hover {
  cursor: pointer;
  background: #ddd;
}

.item.checked {
  color: #fff;
  background: #888;
  text-decoration: line-through;
}

.item.checked:before {
  content: "";
  height: 15px;
  width: 7px;
  border: 2px solid #fff;
  border-width: 0 2px 2px 0;
  position: absolute;
  left: 16px;
  transform: rotate(45deg);
}

.delete {
  border-radius: 5px;
  padding: 10px 16px;
  transition: all .2s;
}

.delete:hover {
  color: #fff;
  background-color: #f44336;
}

